import React, {Component} from 'react';

import './app.css'

import Header from '../header/header';
import Footer from "../footer/footer";
import Title02 from "../title/title-02";
import BonusSection from "../bonus-section/bonus-section";
import BonusSection2 from '../bonus-section-2/bonus-section-2';
import WelcomeSection from "../welcome-section/welcome-section";
import OneClickSection from "../one-click-section/one-click-section";

class App extends Component {
  render() {
    return (
      <div className="app">
        <div className="app-container">
          <Header />
          <WelcomeSection />
          <Title02 />
          <BonusSection />
          <OneClickSection />
          <BonusSection2 />
          <Footer />
        </div>
      </div>
    )
  }
}

export default App;
