import React from "react";

import {useForm} from 'react-hook-form';

import './form.css'

const Form = () => {

  const { register, handleSubmit, errors} = useForm(); // initialise the hook
  const onSubmit = (data) => {
    console.log(data);
  }

  return <>
    <form className="form" onSubmit={handleSubmit(onSubmit)}>
      <div className="input-container">
        <label htmlFor="name" />
        <input
          name="name"
          ref={register({ required: true })}
          className="input mb-20"
          defaultValue="Maxim Gocharenko"
        />
        <div className="input-valid" />
        {errors.name && 'Name is required.'}
      </div>
      <div className="input-container">
        <label htmlFor="mail" />
        <input
          name="mail"
          ref={register({ required: true })}
          className="input mb-20"
          defaultValue="Mail"
        />
        <div className="input-valid" />
        {errors.mail && <div className="error-message">Mail is required.</div>}
      </div>
      <div className="input-container">
        <label htmlFor="comment" />
        <textarea
          name="comment"
          ref={register({ required: true })}
          className="input textarea mb-20"
          defaultValue="Lorem ipsum dolor sit amet, consectetur adipis elit, sed do eiusmod tempor incididunt ut labore"
        />
        <div className="input-valid input-valid-textarea" />
      </div>
      <div className="input-container">
        <label htmlFor="link" />
        <input
          name="link"
          ref={register({ required: true })}
          className="input mb-20"
          defaultValue="Link your online store"
        />
        <div className="input-valid" />
        {errors.link && 'Link is required.'}
      </div>
      <div className="submit-container">
        <div className="submit-btn-wrapper">
          <button className="button submit uppercase" type="submit">Связаться с нами</button>
          <div className="button-border" />
        </div>
      </div>
    </form>
  </>;
};

export default Form;
