import React from 'react';

import './one-click-presentation.css'

const OneClickPresentation = () => <>
  <div className="one-click-presentation">
    <div className="one-click-presentation-circle"/>
    <div className="one-click-presentation-armchair">
      <img src={require('./armchair.png')} alt="armchair" className="one-click-presentation-armchair-img"/>
    </div>
    <div className="one-click-presentation-armchair-circle">
      <img src={require('./ellipse-armchair.png')} alt="Ellipse armchair"
           className="one-click-presentation-armchair-circle-img"/>
    </div>
    <div className="round-animation-block">
      <div className="round-animation">
        <span className="char0">п</span>
        <span className="char1">р</span>
        <span className="char2">о</span>
        <span className="char3">с</span>
        <span className="char4">м</span>
        <span className="char5">о</span>
        <span className="char6">т</span>
        <span className="char7">р</span>
        <span className="char8"> </span>
        <span className="char9">в</span>
        <span className="char10"> </span>
        <span className="char11">д</span>
        <span className="char12">о</span>
        <span className="char13">п</span>
        <span className="char14">о</span>
        <span className="char15">л</span>
        <span className="char16">н</span>
        <span className="char17">е</span>
        <span className="char18">н</span>
        <span className="char19">н</span>
        <span className="char20">о</span>
        <span className="char21">й</span>
        <span className="char22"> </span>
        <span className="char23">р</span>
        <span className="char24">е</span>
        <span className="char25">а</span>
        <span className="char26">л</span>
        <span className="char27">ь</span>
        <span className="char28">н</span>
        <span className="char29">о</span>
        <span className="char30">с</span>
        <span className="char31">т</span>
        <span className="char32">и</span>
      </div>
    </div>
    <div className="touch1">
      <span className="touch1-text">Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur.</span>
      <div className="touch1-line" />
      <div className="touch1-dot" />
    </div>
    <div className="touch2">
      <span className="touch2-text">Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua.</span>
      <div className="touch2-line" />
      <div className="touch2-dot" />
    </div>
  </div>
</>;

export default OneClickPresentation;
