import React from 'react';

import './bonus-section-2.css'
import Title04 from "../title/title-04";

const BonusSection2 = () => <>
  <div className="bonus-section-2">
    <Title04/>
    <div className="bonus-section-delimiter mt-30 mb-55"/>
    <div className="bonus-section-flex">
      <div className="bonus-section-2-block">
        <div>
          <span className="bonus-section-title bonus-section-title-bold">Мы подготавливаем</span>
        </div>
        <div>
          <span className="bonus-section-title bonus-section-title-light"> модели</span>
        </div>
        <div>
          <div className="bonus-section-text mt-20">
            <span>Наши професиональные 3D художники создают точные и фотореалистичные копии Вашего товара</span>
          </div>
        </div>
      </div>
      <div className="bonus-section-2-block">
        <div>
          <span className="bonus-section-title bonus-section-title-bold">Подключаем</span>
          <span className="bonus-section-title bonus-section-title-light"> виджеты</span>
        </div>
        <div>
          <div className="bonus-section-text mt-20">
            <span>Наши розработчики и программисты подключают наш виджеты Вашему интернет магазину и
              любым другим торговым онлайн площадкам (установка приложений не требуеться)</span>
          </div>
        </div>
      </div>
      <div className="bonus-section-2-block">
        <div>
          <span className="bonus-section-title bonus-section-title-bold">Слидите за</span>
          <span className="bonus-section-title bonus-section-title-light"> увеличением продаж</span>
        </div>
        <div>
          <div className="bonus-section-text mt-20">
            <span>Дальше все что нужно это</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</>;

export default BonusSection2;
