import React from 'react';

import './one-click-section.css'
import Title03 from '../title/title-03'
import OneClickPresentation from "./one-click-presentation/one-click-presentation";

const OneClickSection = () => <>
  <div className="one-click-section">
    <div className="one-click-section-title">
      <Title03/>
    </div>
    <div className="one-click-section-presentation">
      <OneClickPresentation/>
    </div>
  </div>
</>;

export default OneClickSection;
